* {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  transition: 0.8s;
}
::-webkit-scrollbar {
  width: 8px;
  background: #9a9a9a80;
}
::-webkit-scrollbar-thumb {
  width: 8px;
  background: rgba(0, 0, 0, 0.6);

  border-radius: 20px;
}
body {
  overflow-x: hidden;
  /* min-height: 400vh; */
  box-sizing: border-box;
  scroll-snap-type: y mandatory;

  margin-top: calc(80vh + 40px);
}
.navbar {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 80vh;
  background: linear-gradient(#9284b3, #bfe5f5, #fff, #fff);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  transition: 0.8s;
  /*scroll-snap-align: start;*/
  background-attachment: fixed;
  animation: navbar 60s linear infinite;
  z-index: 1000;
}
@keyframes navbar {
  0% {
    filter: hue-rotate(0deg);
  }
  50% {
    filter: hue-rotate(360deg);
  }
  100% {
    filter: hue-rotate(0deg);
  }
}
.navbar .quote {
  /* flex-wrap: nowrap; */
  position: absolute;
  top: 0px;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  align-items: center;
  width: 80vw;
  justify-content: center;
  height: 80vh;
  font-family: "Lora";
  font-size: 40px;
  font-weight: 600;
  color: black;
  text-shadow: 0px 0px 2px white;
  display: flex;
  transition: 2s;
}
.navbar.change {
  height: calc(36px + 16px);
  background-position: center;
  background-attachment: fixed;
  z-index: 1000;
}
.navbar.change .quote {
  top: -100vh;
  /* transform: scale(0); */
}
.bg-1 {
  position: fixed;
  top: 0px;
  width: 100%;
  height: 100vh;
  z-index: -1;
  background: white;
  left: 0px;
}
.bg-1::after {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  width: 50%;
  height: 100%;
  background: rgba(255, 0, 0, 0.172);

  filter: blur(40px);
}
.bg-1::before {
  content: "";
  position: absolute;
  top: 0px;
  right: 0px;
  width: 50%;
  height: 100%;
  background: rgba(56, 53, 211, 0.2);
  filter: blur(20px);
}
.PageTitle {
  position: relative;
  width: 100%;
  align-items: center;
  text-align: center;
  margin-bottom: 20vh;

}
.PageTitle .Title {
  position: relative;
  text-align: center;
  font-weight: 720;
  font-size: 26px;
  padding-left: 10px;
  padding-right: 10px;
  border-bottom: 1px solid black;
  box-shadow: 0px 10px 10px -10px red;
  text-shadow: 0px 0px 4px #6a6a6a;
}
.abt
{
  display: flex;
  flex-wrap: nowrap;
}
.Author
{
  width:200px;
  transition: .1s;
  background-attachment: fixed;
  background-position: center;
  mix-blend-mode: multiply;
  position: relative;
  border-radius: "20px";
}
.Blog-Container
{
  position: relative;
  display: flex;
  background:rgba(255,255,255,.4);
  margin: 20px;
  padding: 6px;
  border-radius: 12px;
  margin-top: 6px;
  margin-bottom: 6px;
  z-index: 100;
  box-shadow: 10px 10px 12px -10px rgba(0, 0, 0, .2);
}

.Blog-Container .image
{
  position: relative;
  z-index: 1000;
}

.Blog-Container .image .BlogImage
{
  border-radius: 20px;
  /* box-shadow: -10px -10px 20px -22px black,
  10px 10px 20px -22px black; */
  transition: .4s;
}
.Blog-Container .image .BlogImage:hover
{
  box-shadow: 0px 0px 20px -16px black,
  0px 0px 20px -16px black;
  transform: scale(1.1);

}
.Blog-Container .content
{
  position: relative;
  color:black;
  margin-left: 20px;
  width: 100%;
  padding:10px ;
  transition: .2s;
}
.Blog-Container .content:hover
{
  transform: translateY(-10px);
}
.Blog-Container .content .BlogTitle
{
  font-weight: 900;
  font-size: 24px;
}
.Blog-Container .content .BlogContent
{
  font-weight: 400;
  font-size: 16px;
}
.links
{
  position:absolute;
  top:calc(80vh - 36px - 16px - 14px);
  /* background:red; */
  width:100%;
  height:calc(36px + 16px);
}
.links ul
{
  position:relative;
  display:flex;
  width:fit-content;
  background: rgba(0, 0, 0, 0.1);
  left:50%;
  transform:translateX(-50%);
  border-radius: 10px;
  box-shadow: 0px 0px 26px -20px white;
  overflow: hidden;
}
.links ul li
{
  list-style:none;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.links ul li a 
{
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  display:block;
  /*background:rebeccapurple;*/
  width:calc(60vw / 4);
  min-width:100px;
  max-width:150px;
  white-space:nowrap;
  height:calc(36px + 16px);
  line-height:calc(36px + 16px);  
  text-decoration: none;
  color:black;
  font-weight: 600;
  transition: .2s;
}
.links ul li a:hover
{
  background: rgba(0, 0, 0, .1);
}
.navbar.change .links
{
  top:0px;

}
.navbar.change .links ul
{
  left:100%;
  transform:translateX(-100%);

}